<script setup lang="ts">
import "@/lib/theme"; // Importing to initialize
import HeaderContent from "./components/content/HeaderContent.vue";
import PrivacyLogo from "@/components/icons/IconPrivacyLogo.vue";
import DataConsent from "@/components/DataConsent/DataConsent.vue";
import appExtensionApi from "@/lib/extension/appExtensionApi";
import popupAlert from "@/lib/popupAlert";

const consent = async () => {
  popupAlert("Thank you for using Privacy!", "success", {
    gravity: "top",
    style: {
      width: "50%",
    },
  });

  const tab = await appExtensionApi.getCurrentTab();

  if (tab) {
    await new Promise((resolve) => setTimeout(resolve, 2000));

    await appExtensionApi.context?.tabs?.remove(tab.id!);
  }
};
</script>

<template>
  <HeaderContent class="install-header">
    <div class="p-2 text-center">
      <a href="https://privacy.com" target="_blank">
        <PrivacyLogo aria-label="Privacy" />
      </a>
    </div>
    <div class="header-content">
      <h2>
        Save time with the
        <span>Privacy Add-on</span>
      </h2>
      <span class="sub-heading d-block">
        Create and autofill Privacy Cards with our browser extension
      </span>
      <div class="header-bg"></div>
    </div>
  </HeaderContent>
  <main>
    <section class="my-3 p-3 w-25 mx-auto">
      <DataConsent @consent="consent" />
    </section>
  </main>
</template>

